			"triplePoint": 13.8033,
			"discoveryDate": 1766,
			"context": "Hydrogen gas was first artificially produced in the early 16th century by the reaction of acids on metals. In 176681, Henry Cavendish was the first to recognize that hydrogen gas was a discrete substance, and that it produces water when burned, the property for which it was later named: in Greek, hydrogen means \"water-former\"."
